import React, {Component} from 'react'
import styled from 'styled-components'

export default class extends Component {
  render() {
    return (
      <Feature className="container">
        <div className="title">Mc tiu</div>
        <div className="sub-title text-muted">Chng ti lun hng n mt tang l tron vn v hon ho</div>
        <div className="row">
          <div className="col">
            <div className="shadow box">
              <img src="./src/work.svg" className="feature-icon" alt=""/>
              <p className="text-center">
                Tang l do chng ti m nhin lun c  cao s
                <strong>
                  &nbsp;trang trng</strong>,
                <strong>
                  &nbsp;thnh knh
                </strong>
                &nbsp;v tnh
                <strong>
                  &nbsp;thm m</strong>. Chng ti lun hng n s hon m
              </p>
            </div>
          </div>
          <div className="col">
            <div className="shadow box">
              <img src="./src/effort.svg" className="feature-icon" alt=""/>
              <p className="text-center">
                Chng ti lun lm vic mt cch
                <strong className="">
                  &nbsp;chu o</strong>,
                <strong className="">
                  &nbsp;tn tm</strong>,
                <strong>
                  &nbsp;nhit thnh
                </strong>
                &nbsp;v
                <strong>
                  &nbsp;trch nhim.</strong>
                &nbsp;Chng ti  cao s hi lng ca gia ch.
              </p>
            </div>
          </div>
          <div className="col">
            <div className="shadow box">
              <img src="./src/feedback.svg" className="feature-icon" alt=""/>
              <p className="text-center">
                Gia nh s c t vn
                <strong>&nbsp;r rng
                </strong>
                &nbsp;v
                <strong>&nbsp;chi tit</strong>
                &nbsp;cc loi o quan, nghi l, dch v, ... Chng ti lun mong rng s mang
                n cho gia nh mt tang l
                <strong>&nbsp;trn vn</strong>.
              </p>
            </div>
          </div>
        </div>
      </Feature>
    )
  }
}

const Feature = styled.div `
  font-family: 'Playfair Display', serif;
  font-size: .85rem;
  letter-spacing: .05rem;
  padding: 4rem;
  margin-bottom: 3rem;
  text-align: center;
  .feature-icon{
    width: 6rem;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2rem;
    display: block;
  }
  .box{
    height: 100%;
    padding: 3rem 1.5rem;
  }
  .title{
    margin-bottom: 0;
    font-size: 3rem;
  }
  .sub-title{
    margin-bottom: 5rem;
    letter-spacing: .05rem;
  }
`


