import React, {Component} from 'react'
import styled from 'styled-components'

export default class extends Component {
  render() {
    return (
      <Footer>
        <div className="container">
          <div className="row">
            <div className="col px-5">
              <div className="heading">V chng ti</div>
              <p className="about-us">
                Vi nhiu nm kinh nghim, chng ti m bo s em li dch v chuyn nghip v
                chu ton cho gia nh.
              </p>
            </div>
            <div className="col px-5">
              <div className="heading">Lin h</div>
              <p className="contact">
                <i className="fas contact-icon fa-map-marker-alt fa-sm"></i>
                <span>37 Nguyn Du phng 7, G Vp, H Ch Minh</span>
              </p>
              <p className="contact">
                <i className="fas contact-icon fa-mobile-alt fa-sm"></i>
                <span>0913673661 - Anh Thi</span>
              </p>
              <p className="contact">
                <i className="far contact-icon fa-envelope fa-sm"></i>
                <span>admin@domain.com</span>
              </p>
            </div>
            <div className="col">
              <div className="heading">Gi lm vic</div>
              <p className="open-hour">
                <i className="fas fa-sm contact-icon fa-business-time"></i>Th hai - Ch nht</p>
              <p className="open-hour">
                <i className="far fa-sm contact-icon fa-clock"></i>00:00 - 24:00</p>
            </div>
          </div>
        </div>
      </Footer>
    )
  }
}

const Footer = styled.div `
    background-color: #151515;
    padding: 3rem 0;
    color: #777;
    font-size: .75rem;
    font-family: Lato, sans-serif;
    .heading{
        color: #f5a637;
        letter-spacing: .05em;
        font-weight: bold;
        margin-bottom: 1.5rem;
        text-transform: uppercase;
    }
    .contact-icon{
        min-width: 1.5rem;
    }
    .about-us{
        letter-spacing: .05em;
    }
    .open-hour{
        text-transform: uppercase;
        letter-spacing: .08em;
    }

`


