import React, {Component} from 'react'
import styled from 'styled-components'

export default class extends Component {
  render() {
    return (
      <Service className="container">
        <div className="row">
          <div className="col-6 align-self-lg-center px-5">
            <div className="title mb-2">Trang trng v trn vn</div>
            <div className="sub-title text-muted">
               tang l c thc hin mt cch trang trng v trn vn, Sinh Phc Th cung
              cp nhiu loi dch v, cc nghi thc tang l ph hp vi tn gio, vn ha v
              phong tc ca gia nh.
            </div>
            <div className="row">
              <div className="col-lg-6 px-1">
                <ul className="text-muted category list-group pt-4 text-center">
                  <li className="list-group-item list-group-item-action pointer">Hm nht np trn</li>
                  <li className="list-group-item list-group-item-action pointer">Hm np trn cn</li>
                  <li className="list-group-item list-group-item-action pointer">Hm i Loan G</li>
                </ul>
              </div>
              <div className="col-lg-6 px-1">
                <ul className="text-muted category list-group pt-4 text-center">
                  <li className="list-group-item list-group-item-action pointer">Hm i Loan Cn</li>
                  <li className="list-group-item list-group-item-action pointer">Hm i Loan Ha Tng</li>
                  <li className="list-group-item list-group-item-action pointer">Hm Cng Gio</li>
                </ul>
              </div>
            </div>
          </div>
          <div className="col-2 align-self-end pb-5">
            <div className="little-divider"></div>
            <div className="title">Dch v chng ti</div>
          </div>
          <div className="col-4">
            <img
              src="https://simpleqode.bitbucket.io/touche/assets/img/7.jpg"
              className="w-100 shadow"
              alt=""/>
          </div>

        </div>
      </Service>
    )
  }
}

const Service = styled.div `
  font-family: 'Playfair Display', serif;
  font-size: .85rem;
  letter-spacing: .05rem;
  margin-bottom: 4rem;
  .category{
    padding-left: 0.5rem;
    list-style-type: none
  }
  .little-divider{
    width: 3rem;
    margin-bottom: 1rem;
    border-top: 5px solid #f5a637;
  }
  .title{
    margin-bottom: 0;
    font-size: 1.3rem;
  }
  .sub-title{
    letter-spacing: .05rem;
  }
  .pointer{
    cursor: pointer;
  }
`



